@import url(https://fonts.googleapis.com/css?family=Electrolize|Titillium+Web);
@import url(https://unpkg.com/normalize.css@8.0.1/normalize.css);

:root {
  --primary: 38, 218, 253;
  --primary-light: 161, 236, 251;
  --primary-dark: 1, 120, 144;
  --success: 102, 255, 102;
  --success-accent: 0, 255, 0;
  --alert: 255, 102, 102;
  --alert-accent: 255, 0, 0;
  --disabled: 153, 153, 153;

  --background: 2, 17, 20;
  --link: 172, 249, 251;

  --font-primary: "Titillium Web", sans-serif;
  --font-header: "Electrolize", sans-serif;
}

::-webkit-input-placeholder {
  color: rgb(var(--primary));
}
::-moz-placeholder {
  color: rgb(var(--primary));
}
:-ms-input-placeholder {
  color: rgb(var(--primary));
}
:-moz-placeholder {
  color: rgb(var(--primary));
}
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: rgb(var(--background));
}
::-webkit-scrollbar-thumb {
  border: 1px solid rgb(var(--primary));
  cursor: pointer;
}
::selection {
  color: #021114;
  text-shadow: none;
  background-color: rgb(var(--primary));
}
::-moz-selection {
  color: #021114;
  text-shadow: none;
  background-color: rgb(var(--primary));
}
::-webkit-scrollbar-thumb:hover {
  border-color: rgb(var(--primary-light));
}

body {
  color: rgb(var(--primary));
  background: rgb(var(--background));
  background: linear-gradient(
      -90deg,
      rgba(var(--primary), 0.02) 1px,
      transparent 0
    ),
    linear-gradient(rgba(var(--primary), 0.02) 1px, transparent 0),
    linear-gradient(-90deg, rgba(var(--primary), 0.03) 1px, transparent 0),
    linear-gradient(rgba(var(--primary), 0.03) 1px, transparent 0),
    linear-gradient(
      transparent 4px,
      rgb(var(--background)) 0,
      rgb(var(--background)) 97px,
      transparent 0
    ),
    linear-gradient(-90deg, rgba(var(--primary), 0.15) 1px, transparent 0),
    linear-gradient(
      -90deg,
      transparent 4px,
      rgb(var(--background)) 0,
      rgb(var(--background)) 97px,
      transparent 0
    ),
    linear-gradient(rgba(var(--primary), 0.15) 1px, transparent 0),
    rgb(var(--background));
  background-size: 10px 10px, 10px 10px, 100px 100px, 100px 100px, 100px 100px,
    100px 100px, 100px 100px, 100px 100px;
  background-attachment: fixed;
  line-height: 1.5;
  font-size: 21px;
  font-family: var(--font-primary);
  caret-color: rgb(var(--primary));
  padding: 30px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-header);
  text-transform: uppercase;
  margin: 0 0 20px;
  font-weight: bold;
  color: rgb(var(--primary-light));
  text-shadow: 0 0 4px rgba(var(--primary-light), 0.65);
}

a,
a:visited {
  color: rgb(var(--link));
  text-shadow: 0 0 4px rgba(var(--link), 0.65);
  text-decoration: none;
  cursor: pointer;
  transition: color 250ms ease-out;
}

a:hover {
  color: white;
}

ul,
ol,
dl {
  margin: 0 0 20px 20px;
  display: block;
  padding: 0;
}

ul ul,
ol ol,
dl dl {
  margin-left: 30px;
}

li {
  display: block;
  padding-left: 20px;
  list-style: none;
  counter-increment: item;
}

li:before {
  left: -10px;
  color: rgb(var(--primary-light));
  display: inline-block;
  content: ">>";
  position: relative;
  margin-left: -20px;
}

ol li:before {
  content: counter(item);
}

dt {
  font-style: italic;
  font-weight: bold;
}

blockquote {
  color: rgb(var(--primary));
  margin: 0 0 20px 20px;
  display: block;
  padding: 0 0 0 10px;
  border-left: 4px solid rgb(var(--primary));
}

blockquote.success {
  color: rgb(var(--success));
  border-left-color: 4px solid rgb(var(--success));
}

blockquote.alert {
  color: rgb(var(--alert));
  border-left-color: 4px solid rgb(var(--alert));
}

blockquote.disabled {
  color: rgb(var(--disabled));
  border-left-color: 4px solid rgb(var(--disabled));
}

table {
  width: 100%;
  border-collapse: collapse;
}

tfoot,
thead {
  color: rgb(var(--primary-light));
  text-align: left;
  font-family: var(--font-header);
  font-weight: bold;
  white-space: nowrap;
}

tr {
  border-bottom: 1px solid rgba(var(--primary), 0.325);
}

th,
td {
  padding: 5px;
  vertical-align: top;
}

button {
  margin: 0;
  background: rgba(var(--primary-light), 0.05);
  border: 1px solid rgba(var(--primary-light), 0.5);
  color: rgb(var(--primary-light));
  z-index: 2;
  display: inline-block;
  padding: 10px 20px;
  outline: none;
  position: relative;
  font-size: 15.75px;
  transition: border-color 250ms ease-out;
  line-height: 1;
  user-select: none;
  vertical-align: middle;
  cursor: pointer;
}
button:hover {
  border-color: rgba(var(--primary-light), 1);
}

button.success {
  background: rgba(var(--success-accent), 0.05);
  border: 1px solid rgba(var(--success-accent), 0.5);
  color: rgb(var(--success-accent));
}
button.success:hover {
  border-color: rgba(var(--success), 1);
}

button.alert {
  background: rgba(var(--alert-accent), 0.05);
  border: 1px solid rgba(var(--alert-accent), 0.5);
  color: rgb(var(--alert-accent));
}
button.alert:hover {
  border-color: rgba(var(--alert-accent), 1);
}

button[disabled] {
  background: rgba(var(--disabled) 0.05);
  border: 1px solid rgba(var(--disabled), 0.5);
  color: rgb(var(--disabled));
}

p {
  margin: 1em 0;
}

label {
  font-weight: bold;
  margin: 0 20px 0 0;
}

input,
select {
  padding: 10px 5px;
  color: rgb(var(--primary));
  outline: none;
  background: none;
  border: 0;
  border-bottom: 1px solid rgba(var(--primary-light), 0.65);
  transition: border-bottom 195ms ease-in-out;
}

input[disabled],
select[disabled] {
  border-color: rgb(var(--disabled));
  color: rgba(var(--disabled), 1);
}

input:not([disabled]):focus,
select:not([disabled]):focus,
input:not([disabled]):hover,
select:not([disabled]):hover {
  border-bottom: 1px solid rgba(var(--primary-light), 1);
}
